import React, { Component } from 'react';

class Review extends Component {
    render() {
        return (
            <section className="review-area section-gap" id="review">
            <div className="container">
              <div className="row d-flex justify-content-center">
                <div className="menu-content pb-60 col-lg-10">
                  <div className="title text-center">
                    <h1 className="mb-10">Apa kata mereka</h1>
                    <p>Pujian dari konsumen adalah bukti nyata dari kualitas kopi pilihan kami</p>
                  </div>
                </div>
              </div>						
              <div className="row">
                <div className="col-lg-6 col-md-6 single-review">
                  <img src="img/r1.png" alt="" />
                  <div className="title d-flex flex-row">
                    <h4>Hartono</h4>
                    <div className="star">
                      <span className="fa fa-star checked" />
                      <span className="fa fa-star checked" />
                      <span className="fa fa-star checked" />
                      <span className="fa fa-star" />
                      <span className="fa fa-star" />								
                    </div>
                  </div>
                  <p>
Enak banget kopinyaaaa!!! Pertama sih direkomendasiin temen, tadinya gak nyangka bakal seenak ini.          </p>
                </div>	
                <div className="col-lg-6 col-md-6 single-review">
                  <img src="img/r2.png" alt=""/>
                  <div className="title d-flex flex-row">
                    <h4>Esmeralda</h4>
                    <div className="star">
                      <span className="fa fa-star checked" />
                      <span className="fa fa-star checked" />
                      <span className="fa fa-star checked" />
                      <span className="fa fa-star checked" />
                      <span className="fa fa-star" />								
                    </div>
                  </div>
                  <p>
Wauw beneran enak, tapi harganya mahal juga ya.                 </p>
                </div>	
              </div>
              <div className="row counter-row">
                <div className="col-lg-3 col-md-6 single-counter">
                  <h1 className="counter">2536</h1>
                  <p>Deliveries</p>
                </div>
                
                <div className="col-lg-3 col-md-6 single-counter">
                  <h1 className="counter">2013</h1>
                  <p>Happy Client</p>
                </div>
                <div className="col-lg-3 col-md-6 single-counter">
                  <h1 className="counter">10536</h1>
                  <p>Cups of Coffes</p>
                </div>																		
              </div>
            </div>	
          </section>
        );
    }
}
export default Review;