import React, { Component } from 'react';

class Banner extends Component {
    render() {
        return (
            <div>
                <section className="banner-area" id="home">	
        <div className="container">
          <div className="row fullscreen d-flex align-items-center justify-content-start">
            <div className="banner-content col-lg-7">
              <h6 className="text-white text-uppercase">Layanan Kopi Berlangganan</h6>
              <h1>
                Mulai harimu <br />
                dengan kopi terbaik <br />
                pilihan kami				
              </h1>
              <a href="#" className="primary-btn text-uppercase">Mulai Berlangganan</a>
            </div>											
          </div>
        </div>
      </section>
            </div>
        );
    }
}
export default Banner;